
.carousel {
  padding: 20px 0;
  background-color: #27272E;
}
.carousel .carousel-3d-container .carousel-3d-slide {
    border-color: rgba(0, 0, 0, 0.6);
}
.carousel .carousel-3d-container .carousel-3d-slide figcaption {
      font-size: 12px;
      color: #FFF;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.8);
      border-top: 1px solid rgba(255, 255, 255, 0.4);
      padding: 7px;
      text-align: center;
}
.carousel .carousel-3d-container .carousel-3d-slide figcaption p {
        margin-top: 4px;
}
.carousel .carousel-3d-container .carousel-3d-slide figcaption p:first-child {
          margin-top: 0;
}
.carousel .carousel-3d-container .carousel-3d-controls .prev,
  .carousel .carousel-3d-container .carousel-3d-controls .next {
    font-size: 32px;
    color: #777;
}



