.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.tooltip {
  display: block !important;
  max-width: 250px;
  z-index: 10000;
}
.tooltip .tooltip-inner {
    font-size: 12px;
    line-height: 18px;
    background: #FAFAFA;
    color: #616161;
    border-radius: 3px;
    padding: 10px 13px;
    border: 1px solid #CCC;
}
.tooltip .tooltip-arrow {
    width: 0;
    height: 0;
    border-style: solid;
    position: absolute;
    margin: 5px;
    border-color: #FAFAFA;
}
.tooltip[x-placement^="top"] {
    margin-bottom: 5px;
}
.tooltip[x-placement^="top"] .tooltip-arrow {
      border-width: 5px 5px 0 5px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      bottom: -4px;
      left: calc(50% - 5px);
      margin-top: 0;
      margin-bottom: 0;
}
.tooltip[x-placement^="bottom"] {
    margin-top: 5px;
}
.tooltip[x-placement^="bottom"] .tooltip-arrow {
      border-width: 0 5px 5px 5px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-top-color: transparent !important;
      top: -4px;
      left: calc(50% - 5px);
      margin-top: 0;
      margin-bottom: 0;
}
.tooltip[x-placement^="right"] {
    margin-left: 5px;
}
.tooltip[x-placement^="right"] .tooltip-arrow {
      border-width: 5px 5px 5px 0;
      border-left-color: transparent !important;
      border-top-color: transparent !important;
      border-bottom-color: transparent !important;
      left: -4px;
      top: calc(50% - 5px);
      margin-left: 0;
      margin-right: 0;
}
.tooltip[x-placement^="left"] {
    margin-right: 5px;
}
.tooltip[x-placement^="left"] .tooltip-arrow {
      border-width: 5px 0 5px 5px;
      border-top-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      right: -4px;
      top: calc(50% - 5px);
      margin-left: 0;
      margin-right: 0;
}
.tooltip[aria-hidden='true'] {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity .15s, visibility .15s;
    transition: opacity .15s, visibility .15s;
}
.tooltip[aria-hidden='false'] {
    visibility: visible;
    opacity: 1;
    -webkit-transition: opacity .15s;
    transition: opacity .15s;
}
body {
  font-family: 'Lato', sans-serif;
  overflow-x: hidden;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
  text-rendering: optimizeLegibility;
}
.site-width {
  max-width: 1000px;
  margin: 0 auto;
}
.one-half {
  width: 50%;
}
.one-third {
  width: 33.3333%;
}
.two-third {
  width: 66.6666%;
}
.one-fourth {
  width: 25%;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix:before, .clearfix:after {
  content: '';
  clear: both;
  display: block;
}
.relative {
  position: relative;
}
.section-title {
  font-size: 18px;
  font-weight: 700;
  color: #616161;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.section-subtitle {
  font-size: 12px;
  font-weight: 700;
  color: #888;
  text-align: center;
  display: table;
  white-space: nowrap;
  margin-bottom: 20px;
}
.section-subtitle:before, .section-subtitle:after {
    content: '';
    background-image: url("data:image/png;base64,");
    background-position: right 1em top 50%;
    top: 50%;
    width: 50%;
    background-repeat: no-repeat;
    position: relative;
    display: table-cell;
}
.section-subtitle:after {
    background-position: left 1em top 50%;
}
.bullet {
  font-size: 10px;
  font-weight: 700;
  line-height: 10px;
  color: #333;
  display: inline-block;
  padding: 3px;
  border: 1px solid #C7A605;
  border-radius: 2px;
  background-color: #C7A605;
  margin: 2px 5px 2px 0;
  text-decoration: none;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.bullet:hover {
    background-color: #AE9204;
}
.color-yellow {
  color: #C7A605;
}
.color-twitter {
  color: #0084b4;
}
.color-facebook {
  color: #3B5998;
}
.color-instagram {
  color: #BC2A8D;
}
.table {
  display: table;
}
.table .table-row {
    display: table-row;
}
.table .table-cell {
    display: table-cell;
    padding: 3px 5px;
}
.table .table-cell:first-child {
      padding-left: 0;
}
.table .table-cell:last-child {
      padding-right: 0;
}
.text-regular {
  font-size: 14px;
  line-height: 22px;
}
.text-small {
  font-size: 12px;
}
.text-bold {
  font-weight: bold;
}



